import React from 'react'
import { Link } from 'react-router-dom'

function Menu({active}) {

  return (
    <nav className={`menu ${active?'menu-active':undefined}`}>
        <Link className='menu-button' to='/'>koly</Link>
        <Link className='menu-button' to='/new'>Vytvoit nov</Link>
        <Link className='menu-button' to='/login'>Pihlsit se</Link>
        <Link className='menu-button' to='/register'>Zaregistrovat se</Link>
    </nav>
  )
}

export default Menu